import React from 'react';

const Role = () => {
	return (
		<div id='Role' >
			<div className='content'>
				<h1 id='title' className='titleFont red contentTitle'>Our Challenge</h1> <br />
				<p className='normalText contentText'>
					 Traveling should be experienced from the very moment you select
					 which company will be your partner. As Veru wants to create 
					 an unique traveling experience for their customers, we needed an 
					 identity that vibrated along with that idea. It's a big challenge, 
					 especially considering the competition in the region has been strongly 
					 established for decades. Therefore, Veru should feel part of the existing 
					 ecosystem but always highlighting its own identity.
				</p>
				<h1 id='title' className='titleFont red contentTitle'>My Role</h1> <br />
				<p className='normalText contentText'>
					During this stage of the process as the UX/UI Designer, I started doing 
					the initials wireframes which we later on used to start building and designing 
					the website. It was very crucial during this moment to apply the iterative 
					design since we had two very specific user journey in mind each one with their 
					own needs. Most of the information that we used was gathered through card 
					sorting, UX research and user tests to see how the potentials users interact with our product. 
				</p>
			</div>
			<section className='bigImg veru_img02 bgImg'> </section>
		</div>
	)
}

export default Role;