import React from 'react';

const Presentation = () => {
	return (
		<div id='Presentation' >
			<div className='content'>
				<h1 id='title' className='titleFont red contentTitle'>About the project</h1> <br />
				<p className='normalText contentText'>
					After the first process in which we defined Veru's identity, we continue working
					 in the design and construction of Veru's website. To accomplish this 
					 task, we kept in mind our previous investigation during all the 
					 process, especially the visual identity and personas. This last element was 
					 particularly useful because it helped us to define better our possible 
					 user journey on the website. 
				</p>
			</div>
			<section className='bigImg veru_img01 bgImg'> </section>
		</div>
	)
}

export default Presentation;