import React, { Component } from 'react';
import design01 from './../../resources/Design_01.jpg';
import design02 from './../../resources/Design_02.jpg';

class Design extends Component {
	render () {
		return (
			<div id='Design' >
				<section className='designContainer' style={{paddingTop: '7%'}} >
					<div className='design'>
						<h1 id='title' className='titleFont red contentTitle'>Design Process</h1> <br />
						<p className='normalText contentText'>
							 Since the beginning, we knew that we wanted to offer two main experience on our website.
							  The first was tailored to our user that wants to buy its travel package without having 
							  to lose its time navigating through all our webpages, for them we create the first user 
							  journey considering user's pain points and needs. On the other hand, for our second 
							  group of users, the ones that may want to know a little more about us, and need content 
							  that helps them to select us as their travel company we design a second path in which 
							  the user can know about the company, about our experiences, locations, and services. 
							  Through this entire journey, the user will be accompanied by not only text but also 
							  others mediums such as photographs and videos that are designed to give them the best 
							  experience while they get to know the mark. 
						</p>
						<h1 id='title' className='titleFont red innerTittle'>Wireframes</h1> <br />
					</div>
					<div className='smallImg bgImg'>
						<img className='designImg' src={design01} alt='keywords' />
					</div>
				</section>
				<section className='designContainer' >
					<div className='design'>
						<h1 id='title' className='titleFont red innerTittle'>Color Moods</h1> <br />
					</div>
					<div className='smallImg bgImg'>
						<img className='designImg' src={design02} alt='ColorMoods' />
					</div>
				</section>
				<section className='designContainer' >
					<div className='design'>
						<p className='normalText contentText'>
							 At this point, we decided to create personas to have a deep
							  understanding of how our design might be perceived by our target
							   audience point of view. This step allowed us to put into practice 
							   our empathy for our user, thinking about the product not from the 
							   point of view of the designer but how it would be perceived by 
							   each different persona with their own particularities.
						</p>
					</div>
				</section>
			</div>
		)
	}	
}

export default Design;