import React from 'react';
import Personas01 from './../../resources/Personas01.jpg';
import Personas02 from './../../resources/Personas02.jpg';
import Personas03 from './../../resources/Personas03.jpg';

const Personas = () => {
	return (
		<div id='Personas' >
				<section className='designContainer' style={{paddingTop: '7%'}} >
					<div className='design'>
						<h1 id='title' className='titleFont red contentTitle'>Personas</h1> <br />
					</div>
					<div className='smallImg bgImg personasImg'>
						<img className='designImg' src={Personas01} alt='keywords' />
						<img className='designImg' src={Personas02} alt='keywords' />
						<img className='designImg' src={Personas03} alt='keywords' />
					</div>
				</section>
		</div>
	)
}

export default Personas;