import React from 'react';
import vision01 from './../../resources/Vision_01.jpg';
import vision02 from './../../resources/Vision_02.jpg';
import vision03 from './../../resources/Vision_03.jpg';
import vision04 from './../../resources/Vision_04.jpg';
import vision05 from './../../resources/Vision_05.jpg';
import vision06 from './../../resources/Vision_06.jpg';

const Vision = () => {
	return (
		<div id='Vision' >
			<div className='content'>
				<h1 id='title' className='titleFont red contentTitle'>Vision</h1> <br />
				<p className='normalText contentText'>
					After our research, we created an attractive color palette that empathized
				 	and visually transmitted Veru's identity. The primary color is a slightly
				  	desaturated and opaque green tone, accompanied by secondary colors 
				  	such as a yellow shade light and an intense blue (all slightly opaque 
			  		tones) resulting in a color palette quite elegant which retains an 
				  	integral feeling and interest in the nature, this palette sympathizes 
				  	with Veru's idea of showing itself as a company connected to the 
				  	environment.
					  <br />
					  <br />
					We chose a typeface for the content of the brand which was in harmony with 
					the color palette we previously defined. Then, we decided to choose a font that 
					was easy to access and especially easy to read.
					<br />
					<br />
					Finally, as our goal was to establish that the logo should also work as an icon
					during its creation, we came to the idea of a paper plane and after 
					some discussions, we saw how it can represent Veru's features both metaphorical 
					and symbolically. 
				</p>
			</div>
			<section className='designContainer' >
					<div className='smallImg bgImg'>
						<img className='designImg' src={vision04} alt='ColorMoods' />
					</div>
					<div className='smallImg bgImg'>
						<img className='designImg' src={vision02} alt='ColorMoods' />
					</div>
					<div className='smallImg bgImg'>
						<img className='designImg' src={vision01} alt='ColorMoods' />
					</div>
					<div className='smallImg bgImg'>
						<img className='designImg' src={vision03} alt='ColorMoods' />
					</div>
					<div className='smallImg bgImg'>
						<img className='designImg' src={vision05} alt='ColorMoods' />
					</div>
					<div className='smallImg bgImg'>
						<img className='designImg' src={vision06} alt='ColorMoods' />
					</div>
			</section>
			<section className='bigImg veru_img01 bgImg'> </section>
		</div>
	)
}

export default Vision;